  glosario: [
    {
    "termino": "Recorrido turístico",
    "significado": "Trayecto planificado que integra atractivos, actividades y servicios turísticos, diseñado para ofrecer experiencias significativas al visitante en un tiempo determinado."
  },
  {
    "termino": "Guion turístico",
    "significado": "Documento estructurado que organiza los contenidos, secuencias temáticas y recursos interpretativos de un recorrido, orientando la narrativa del guía y la experiencia del visitante."
  },
  {
    "termino": "Perfil del turista",
    "significado": "Conjunto de características sociodemográficas, motivacionales y de comportamiento que permiten segmentar a los visitantes y adaptar las experiencias a sus intereses."
  },
  {
    "termino": "Segmentación turística",
    "significado": "Estrategia de mercado que divide a los turistas en grupos homogéneos según criterios como edad, intereses, nivel económico o tipo de viaje, para ofrecer servicios personalizados."
  },
  {
    "termino": "Atractivo turístico",
    "significado": "Elemento natural, cultural o creado por el ser humano que motiva el desplazamiento del visitante y genera interés por conocer o experimentar algo diferente."
  },
  {
    "termino": "Destino turístico",
    "significado": "Lugar geográfico que concentra una oferta integral de atractivos, servicios e infraestructura, preparado para recibir y atender visitantes."
  },
  {
    "termino": "Recursos interpretativos",
    "significado": "Herramientas o medios utilizados para comunicar los valores de los atractivos, como paneles, materiales audiovisuales, demostraciones o relatos."
  },
  {
    "termino": "Sostenibilidad turística",
    "significado": "Modelo de gestión que busca equilibrar el desarrollo económico, la equidad social y la conservación ambiental del destino, garantizando beneficios a largo plazo."
  },
  {
    "termino": "Contexto sociocultural",
    "significado": "Conjunto de valores, tradiciones, costumbres e interacciones sociales que definen la identidad cultural de una comunidad receptora o destino turístico."
  },
  {
    "termino": "Contexto económico",
    "significado": "Entorno de condiciones financieras y productivas que influyen en la viabilidad, desarrollo y sostenibilidad de la actividad turística en un territorio."
  },
  {
    "termino": "Contexto ambiental",
    "significado": "Conjunto de factores naturales —como clima, biodiversidad y paisaje— que condicionan la planificación y gestión del turismo."
  },
  {
    "termino": "Planificación turística",
    "significado": "Etapa inicial del diseño de un recorrido o programa, donde se definen objetivos, recursos, tiempos, riesgos y estrategias para garantizar una experiencia ordenada y segura."
  },
  {
    "termino": "Evaluación del recorrido",
    "significado": "Fase final del proceso turístico donde se analizan los resultados de la experiencia, la satisfacción del visitante y las oportunidades de mejora."
  },
  {
    "termino": "Capacidad de carga turística",
    "significado": "Cantidad máxima de visitantes que puede recibir un sitio sin generar efectos negativos en el entorno natural, cultural o social, ni deteriorar la experiencia del turista."
  },
  {
    "termino": "Interpretación del patrimonio",
    "significado": "Proceso mediante el cual el guía traduce los significados del patrimonio cultural y natural en experiencias comprensibles y memorables para el visitante."
  },
  {
    "termino": "Comunicación interpretativa",
    "significado": "Estrategia de transmisión de mensajes que combina información, emoción y participación del visitante para generar comprensión y conexión con los lugares visitados."
  },
  {
    "termino": "Técnica sensorial",
    "significado": "Método interpretativo que estimula los sentidos para crear experiencias turísticas inmersivas y memorables."
  },
  {
    "termino": "Tecnología digital",
    "significado": "Dispositivos como tabletas, celulares, auriculares o herramientas de realidad aumentada usados para enriquecer la experiencia turística."
  },
  {
    "termino": "Animación turística",
    "significado": "Acciones y dinámicas lúdicas que promueven la participación activa del visitante, fortaleciendo la interacción y el aprendizaje durante el recorrido."
  },
  {
    "termino": "Expresión corporal",
    "significado": "Uso consciente de gestos, posturas, desplazamientos y contacto visual como medio de comunicación no verbal que complementa la guianza turística."
  },
  ],
